.roles{
    display: flex;
    align-items: center;
    justify-content: center;
}

.cont h5{
    font-weight: 700;
    color: blueviolet;
}
.cont h1{
    font-weight: 700;
    font-size: 40px;
    margin: 30px 0;
}

.roles .inp{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.roles .inp input{
    width: 70%;
    margin-right: 10px;
   
}
.roles .inp button{
   background: blueviolet; 
   color: #fff;
}






@media screen and (max-width: 600px) {
    .roles .inp{
    flex-direction: column;
   
}

.roles .inp input{
    width: 100%;

}
.roles .inp button{
    width: 100%;
    margin-top: 10px;

}
.cont h1{
    font-weight: 700;
    font-size: 25px;
}

}